<template> 
    <div v-if="!item.hidden && item.children" class="menu-wrapper">
        <!-- a--> 
        <template v-if="hasOneShowingChild(item) && (!onlyOneChild.children||onlyOneChild.noShowingChildren)&&!item.alwaysShow">
            <router-link :to="resolvePath(onlyOneChild.path)">
                <el-menu-item :index="resolvePath(onlyOneChild.path)" :class="{'submenu-title-noDropdown':!isNest}"> 
                    <item v-if="onlyOneChild.meta" :icon="onlyOneChild.meta.icon||item.meta.icon" :title="onlyOneChild.meta.title" />
                </el-menu-item>
            </router-link>
        </template>
 
        <el-submenu  v-else :index="resolvePath(item.path)">
            <!-- b -->
            <template slot="title"> 
              <item v-if="item.meta" :icon="item.meta.icon" :title="item.meta.title" />

            </template>

          <template v-for="child in item.children" v-if="!child.hidden">
               <sidebar-item
                  v-if="child.children && child.children.length>0"
                  :is-nest="true"
                  :item="child"
                  :key="child.path"
                  :base-path="resolvePath(child.path)"
                  class="nest-menu" />
              <!--  c-->
              <router-link v-else :to="resolvePath(child.path)" :key="child.name">
                  <el-menu-item :index="resolvePath(child.path)" v-if="isShow(child)">
                       <item v-if="child.meta" :icon="child.meta.icon" :title="child.meta.title"/> 
                  </el-menu-item>
              </router-link>
          </template>
        </el-submenu>
    </div>
</template>

<script>
import { getOwner } from "@/utils/auth";

import path from "path";
import Item from "./item";

export default {
  name: "SidebarItem",
  components: {
    Item
  },
  props: {
    item: {
      type: Object,
      required: true
    },
    isNest: {
      type: Boolean,
      default: false
    },
    basePath: {
      type: String,
      default: ""
    }
  },
  data() {
    return {
      onlyOneChild: null
    };
  },
  methods: {
    isShow(item) {
      // if (getOwner()) {
      //   if (item.name == "exit") {
      //     return false;
      //   }
      // } else {
      //   if ((item.name == "info") | (item.name == "change")) {
      //     return false;
      //   }
      // }
      // if (item.meta) {
      //   return true;
      // } else {
      //   return false;
      // }

      /* 后期删除 */
      return true 
    },
    hasOneShowingChild(item) {
      const filters = item.children.filter(item => {
        if (item.hidden) {
          return false;
        } else {
          this.onlyOneChild = item;
          return true;
        }
      });

      if (filters.length === 1) {
        return true;
      }
      if (filters.length === 0) {
        this.onlyOneChild = { ...item, path: "", noShowingChildren: true };
        return true;
      }
      return false;
    },
    resolvePath(routePath) {
      return path.resolve(this.basePath, routePath);
    }
  }
};
</script>

<style>
</style>



