<template>
    <section class="app-main">
        <transition name='fade' mode='out-in'>
            <!-- 缓存页面 -->
            <!-- <keep-alive :include="cachedViews"> -->
                <router-view/>
            <!-- </keep-alive> -->
        </transition>
    </section>
</template>

<script>
export default {
  name: "appMain",
  computed: {
    cachedViews() {
      return this.$store.state.tagsView.cachedViews;  // 判断缓存页面有哪些
    },
    key() {
      return this.$route.fullPath; // 为了让页面刷新，暂时用不到
    }
  }
};
</script>

<style rel="stylesheet/scss" lang="scss" scoped >
.fade-enter,
.fade-leave-active {
  opacity: 0;
}
.fade-enter-active,
.fade-leave-active {
  transition: all 0.3s;
}
.app-main {
  /*84 = navbar + tags-view = 50 +34 */
  // min-height: calc(100vh - 84px);
  // width: calc(100% - 40px);
  padding: 20px;
  position: relative;
  background: #f0f2f5;
  height: 100%;
  overflow: auto;
}
</style>



