import Vue from 'vue'
import Vuex from 'vuex'
Vue.use(Vuex)

import app from './module/app'
import permission from './module/permission'
import user from './module/user'

import getters from './getters'
const store = new Vuex.Store({
    modules: {
        app,
        permission,
        user
    },
    getters
}) 

export default store


