<template>
    <el-scrollbar wrap-class="scrollbar-wrapper">
        <div class="user">
            <img class="avatar" :src="userInfo.head_portrait">
            <div class="info">
                <p class="greet">Welcome!</p>
                <p class="name">{{userInfo.realname}}</p>
            </div>
 
        </div>
        <el-menu
            :show-timeout="200"
            :default-active="$route.path"
            :collapse="isCollapse"
            mode="vertical"
            background-color="#2c3e50"
            text-color="#bfcbd9"
            active-text-color="#409EFF">

            <!-- 循环每一个路由项 -->
            <sidebar-item v-for="route in permission_routers" :key="route.path" :item="route" :base-path="route.path"/>

        </el-menu>
    </el-scrollbar>
</template>

<script>
import sidebarItem from "./sidebarItem";

import { mapGetters } from "vuex";
import { constantRouterMap } from '@/router'

export default {
  components: {
    sidebarItem
  },
  data () {
    return {
      permission_routers: []
    }
  },
  computed: {
    ...mapGetters(["sidebarOpen","userInfo"]),
    isCollapse() {
      return !this.sidebarOpen;
    }
  },
  created () { 
    this.$store.dispatch('GetInfo')
    this.permission_routers = constantRouterMap 
  }
};
</script>

<style lang='scss' scoped>
.user {
  display: flex;
  align-items: center;
  justify-content: center;
  width: 100%;
  height: 84px;
  color: rgba(201, 211, 223, 1);
  background: rgba(67, 89, 110, 1);
  .avatar {
    width: 60px;
    height: 60px;
    margin: auto 12px;
    border-radius: 50%;
  }
  .info {
    flex: 1;
  }
  p.greet {
    font-size: 16px;
  }
  p.name {
    font-size: 18px;
    font-weight: bold;
  }
}
</style>



