import request from '@/utils/request'

export function checkMobile(mobile) {
  return request({
    url: '/broker/checkMobile',
    method: 'post',
    data: {
      mobile: mobile
    }
  })
}

export function login(data) {
  return request({
    url: '/Agentlogin/login',
    method: 'post',
    data
  })
}
export function loginout(data) {
  return request({
    url: '/Agentlogin/loginout',
    method: 'get'
  })
}
/* 退出门店 */
export function agentSignOut(data) {
  return request({
    url: '/broker/agentSignOut',
    method: 'post',
    data
  })
}

export function sendMessage(mobile) {
  return request({
    url: '/Agentlogin/sendMessage',
    method: 'post',
    data: {
      mobile: mobile
    }
  })
}


export function getAgentInfo() {
  return request({
    url: '/broker/agentInfo',
    method: 'get',
  })
}


export function perfectInfo(data) {
  return request({
    url: '/broker/perfectInfo',
    method: 'post',
    data
  })
}

export function editPhoneOne(data) {
  return request({
    url: '/broker/editPhoneOne',
    method: 'post',
    data
  })
}





 


