const mfilters = {
    /* 经纪人的标签判断 tag */
    markImg: function(data) {
      let imgSrc;
      if (data == '独立经纪人') {
        imgSrc = require("@/assets/image/aggentBase.png");
      } else if (data == '高级经纪人') {
        imgSrc = require("@/assets/image/aggentHigh.png");
      }else if (data == '资深经纪人') {
        imgSrc = require("@/assets/image/aggentLast.png");
      }else if (data == '门店经纪人') {
        imgSrc = require("@/assets/image/aggentShop.png");
      }else if (data == '金牌顾问') {
        imgSrc = require("@/assets/image/aggentTop.png");
      }
  
      return imgSrc;
    },
    listImg: function (data) {
      let imgSrc; 
      if (data == 1 | data == 3 | data == 2) {
        imgSrc = require("@/assets/image/recommen.png");
      } else if (data == 4 | data == 5 | data == 6) {
        imgSrc = require("@/assets/image/recommenHome.png");
      } else if (data == 7 | data == 8){
        imgSrc = require("@/assets/image/advertising.png");
      } else {
        imgSrc = ''
      }
  
      return imgSrc;
    },
    formatDate(time, cFormat) {
      if (arguments.length === 0) {
        return null
      }
      const format = cFormat || '{y}-{m}-{d} {h}:{i}:{s}'
      let date
      if (typeof time === 'object') {
        date = time
      } else {
        if (('' + time).length === 10) time = parseInt(time) * 1000
        date = new Date(time)
      }
      const formatObj = {
        y: date.getFullYear(),
        m: date.getMonth() + 1,
        d: date.getDate(),
        h: date.getHours(),
        i: date.getMinutes(),
        s: date.getSeconds(),
        a: date.getDay()
      }
      const time_str = format.replace(/{(y|m|d|h|i|s|a)+}/g, (result, key) => {
        let value = formatObj[key]
        if (key === 'a') return ['一', '二', '三', '四', '五', '六', '日'][value - 1]
        if (result.length > 0 && value < 10) {
          value = '0' + value
        }
        return value || 0
      })
      return time_str
    }
  };
  export default mfilters;
  


