import { getAgentInfo, login } from "@/api/login"; 
const user = {
  state: { 
    userInfo: {}, 
  },
  mutations: { 
    SET_USERINFO(state, data) {
      state.userInfo = data;
    }
  },
  actions: {
    // 登陆
    LoginByPhone({ commit, state }, info) {
      return new Promise((resolve, reject) => {
        login(info)
          .then(res => { 
            resolve(res);
          })
          .catch(error => { 
            reject(error);
          });
      });
    }, 
    GetInfo({ commit, state }) {
      return new Promise((resolve, reject) => {
        getAgentInfo()
          .then(res => {
            commit("SET_USERINFO", res.data.data);
            resolve(res);
          })
          .catch(error => {
            reject(error);
          });
      });
    }
  }
};

export default user;



