import Cookies from 'js-cookie'

const app = {
    state: { 
        sidebarOpen: !+Cookies.get('sidebarStatus'), 
        esfcondition: {},
        rentcondition: {},
        storecondition: {},
    },
    mutations: {
        TOGGLE_SIDEBAR: state => {
            if (state.sidebarOpen) {
                Cookies.set('sidebarStatus', 1)
            } else {
                Cookies.set('sidebarStatus', 0)
            }

            state.sidebarOpen = !state.sidebarOpen
        },
        SET_ESFCONDITION: (state,val) => {  
            state.esfcondition = Object.assign({},val)  
        },
        SET_RENTCONDITION: (state,val) => {  
            state.rentcondition = Object.assign({},val)  
        },
        SET_STORECONDITION: (state,val) => {  
            state.storecondition = Object.assign({},val)  
        }, 
    }
}

export default app


