import Cookies from 'js-cookie'

const AggentId = 'agent_id'

const owner = 'is_shopowner'

export function getId() {
  return Cookies.get(AggentId)
}

export function setId(AggentId) {
  return Cookies.set(AggentId, AggentId)
} 

export function getOwner () {
 
  if (Cookies.get(owner) == 1) { 
    return true
  } else {  
    return false
  } 
}


