<template> 
    <button class="hamburger" @click="toggleClick" :class="[active, type]">
        <span class="icon"></span>
    </button>
</template>

<script>
export default {
  // 默认类型cancel，其他plus，minus，hamburger-vertical，arrow-left，arrow-right
  props: {
    type: { type: String, default: "vertical" },
    isTurn: {type: Boolean, default: false},
    toggleClick: {type: Function, default: null}
  },
  data() {
    return {
    };
  },
  computed: {
    active() {
      if (!this.isTurn) {
        return "active";
      }
    }
  }
};
</script>

<style> 
.hamburger {
  position: relative;
  top: -20px;
  margin: 0 10px;
  font-size: 21px;
  display: inline-block;
  width: 1em;
  height: 1em;
  padding: 0;

  cursor: pointer;

  transition: transform 0.2s ease-in-out;
  -moz-transition: transform 0.2s ease-in-out;
  -webkit-transition: transform 0.2s ease-in-out;

  vertical-align: middle;

  border: 0 none;
  background: transparent;
}

/**
 * Button height fix for Firefox
 */
.hamburger::-moz-focus-inner {
  padding: 0;
  border: 0 none;
}

/**
 * Focus fix for Chrome
 */
.hamburger:focus {
  outline: 0;
}

/**
 * Styling the icon
 */
.hamburger:before,
.hamburger:after {
  content: "";
}

.hamburger:before,
.hamburger .icon,
.hamburger:after {
  display: block;

  width: 100%;
  height: 0.2em;
  margin: 0 0 0.2em;

  transition: transform 0.2s ease-in-out;
  -moz-transition: transform 0.2s ease-in-out;
  -webkit-transition: transform 0.2s ease-in-out;

  border-radius: 0.05em;
  -moz-border-radius: 0.05em;
  -webkit-border-radius: 0.05em;

  background: #596c7d;
}

/**
 * Styles for the active `.hamburger` icon
 */
.hamburger.active:before,
.hamburger.active .icon,
.hamburger.active:after {
  background: #2c3e50;
}

/* 各种类型 */
/**
 * VERTICAL HAMBURGER
 */
.hamburger.vertical.active {
  transform: rotate(270deg);
  -moz-transform: rotate(270deg);
  -webkit-transform: rotate(270deg);
}

/**
 * CLOSE/CANCEL/CROSS
 */
.hamburger.cancel.active .icon {
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
}

.hamburger.cancel.active:before {
  transform: translateY(0.4em) rotate(135deg);
  -moz-transform: translateY(0.4em) rotate(135deg);
  -webkit-transform: translateY(0.4em) rotate(135deg);
}

.hamburger.cancel.active:after {
  transform: translateY(-0.4em) rotate(-135deg);
  -moz-transform: translateY(-0.4em) rotate(-135deg);
  -webkit-transform: translateY(-0.4em) rotate(-135deg);
}

/**
 * PLUS
 */
.hamburger.plus.active .icon {
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
}

.hamburger.plus.active:before {
  transform: translateY(0.4em) rotate(90deg);
  -moz-transform: translateY(0.4em) rotate(90deg);
  -webkit-transform: translateY(0.4em) rotate(90deg);
}

.hamburger.plus.active:after {
  transform: translateY(-0.4em) rotate(180deg);
  -moz-transform: translateY(-0.4em) rotate(180deg);
  -webkit-transform: translateY(-0.4em) rotate(180deg);
}

/**
 * MINUS/DASH
 */
.hamburger.minus.active {
  transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}

.hamburger.minus.active .icon {
  transform: scale(0);
  -moz-transform: scale(0);
  -webkit-transform: scale(0);
}

.hamburger.minus.active:before {
  transform: translateY(0.4em);
  -moz-transform: translateY(0.4em);
  -webkit-transform: translateY(0.4em);
}

.hamburger.minus.active:after {
  transform: translateY(-0.4em);
  -moz-transform: translateY(-0.4em);
  -webkit-transform: translateY(-0.4em);
}

/**
 * LEFT ARROW
 */
.hamburger.arrow-left.active {
  transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}

.hamburger.arrow-left.active:before {
  width: 0.6em;
  transform: translateX(0.4em) translateY(0.2em) rotate(45deg);
  -moz-transform: translateX(0.4em) translateY(0.2em) rotate(45deg);
  -webkit-transform: translateX(0.4em) translateY(0.2em) rotate(45deg);
}

.hamburger.arrow-left.active .icon {
  border-radius: 0.1em 0.25em 0.25em 0.1em;
  -moz-border-radius: 0.1em 0.25em 0.25em 0.1em;
  -webkit-border-radius: 0.1em 0.25em 0.25em 0.1em;
}

.hamburger.arrow-left.active:after {
  width: 0.6em;
  transform: translateX(0.4em) translateY(-0.2em) rotate(-45deg);
  -moz-transform: translateX(0.4em) translateY(-0.2em) rotate(-45deg);
  -webkit-transform: translateX(0.4em) translateY(-0.2em) rotate(-45deg);
}

/**
 * RIGHT ARROW
 */
.hamburger.arrow-right.active {
  transform: rotate(180deg);
  -moz-transform: rotate(180deg);
  -webkit-transform: rotate(180deg);
}

.hamburger.arrow-right.active:before {
  width: 0.6em;
  transform: translateX(0) translateY(0.2em) rotate(-45deg);
  -moz-transform: translateX(0) translateY(0.2em) rotate(-45deg);
  -webkit-transform: translateX(0) translateY(0.2em) rotate(-45deg);
}

.hamburger.arrow-right.active .icon {
  border-radius: 0.25em 0.1em 0.1em 0.25em;
  -moz-border-radius: 0.25em 0.1em 0.1em 0.25em;
  -webkit-border-radius: 0.25em 0.1em 0.1em 0.25em;
}

.hamburger.arrow-right.active:after {
  width: 0.6em;
  transform: translateX(0) translateY(-0.2em) rotate(45deg);
  -moz-transform: translateX(0) translateY(-0.2em) rotate(45deg);
  -webkit-transform: translateX(0) translateY(-0.2em) rotate(45deg);
}
</style>



