import Vue from "vue";
import Router from "vue-router";
import { getId } from "@/utils/auth"; 

import Layout from "@/views/layout/Layout";

Vue.use(Router);

export const constantRouterMap = [
  {
    path: "/",
    component: Layout,
    redirect: "/home",
    children: [
      {
        path: "home",
        name: "home",
        component: () => import("@/views/home/index"),
        meta: { title: "中心首页", icon: "icon-home", noCache: true }
      }
    ]
  },
  {
    path: "/login",
    name: "login",
    component: () => import("@/views/login/index"),
    hidden: true
  },
  {
    path: "/service",
    component: Layout,
    meta: { title: "增值服务", icon: "icon-Valueaddedservice" },
    children: [
      {
        path: "showmeal",
        name: "showmeal",
        component: () => import("@/views/service/show/showMeal"),
        meta: { title: "展示型型套餐" }
      },
      {
        path: "funmeal",
        name: "funmeal",
        component: () => import("@/views/service/func/funMeal"),
        meta: { title: "功能型套餐" }
      }
    ]
  },
  {
    path: "/shop",
    component: Layout,
    meta: { title: "门店管理", icon: "icon-Generalvisit" },
    children: [
      {
        path: "info",
        name: "info",
        component: () => import("@/views/shop/info"),
        meta: { title: "门店信息" }
      },
      {
        path: "members",
        name: "members",
        component: () => import("@/views/shop/members"),
        meta: { title: "团队成员" }
      },
      {
        path: "change",
        name: "change",
        component: () => import("@/views/shop/change"),
        meta: { title: "变更店长" }
      },
      {
        path: "addmember",
        name: "addmember",
        hidden: true,
        component: () => import("@/views/shop/addmember")
      }
    ]
  },
  {
    path: "/community",
    component: Layout,
    meta: { title: "小区", icon: "icon-Residentialquarters" },
    children: [
      {
        path: "add",
        name: "addcommunity",
        component: () => import("@/views/community/add"),
        meta: { title: "录入" }
      },
      {
        path: "lists",
        name: "communitylists",
        component: () => import("@/views/community/lists"),
        meta: { title: "查看" }
      }
    ]
  },
  {
    path: "/secondhouse",
    component: Layout,
    meta: { title: "二手房", icon: "icon-Second-handhousing" },
    children: [
      {
        path: "add",
        name: "addsecondhouse",
        component: () => import("@/views/secondhouse/add"),
        meta: { title: "录入房源" }
      },
      {
        path: "lists",
        name: "secondhouselists",
        component: () => import("@/views/secondhouse/lists"),
        meta: { title: "房源库" }
      }
    ]
  },
  {
    path: "/rent",
    component: Layout,
    meta: { title: "租房", icon: "icon-Renting" },
    children: [
      {
        path: "add",
        name: "addrent",
        component: () => import("@/views/rent/add"),
        meta: { title: "录入房源" }
      },
      {
        path: "lists",
        name: "rentlists",
        component: () => import("@/views/rent/lists"),
        meta: { title: "房源库" }
      }
    ]
  },
  {
    path: "/store",
    component: Layout,
    meta: { title: "商铺", icon: "icon-personalstores" },
    children: [
      {
        path: "add",
        name: "addstore",
        component: () => import("@/views/store/add"),
        meta: { title: "录入房源" }
      },
      {
        path: "lists",
        name: "storelists",
        component: () => import("@/views/store/lists"),
        meta: { title: "房源库" }
      }
    ]
  },
  {
    path: "/entrustHouse",
    component: Layout,
    redirect: "/entrustHouse/index",
    children: [
      {
        path: "index",
        component: () => import("@/views/entrustHouse/index"),
        name: "entrustHouse",
        meta: {
          title: "委托房源",
          icon: "icon-Authorizedhousings",
          noCache: true
        }
      }
    ]
  },
  {
    path: "/account",
    component: Layout,
    meta: { title: "账户资料", icon: "icon-Accountinformation" },
    children: [
      {
        path: "user",
        name: "user",
        component: () => import("@/views/account/user"),
        meta: { title: "个人信息" }
      },
      {
        path: "updatePhone",
        name: "updatePhone",
        component: () => import("@/views/account/updatePhone"),
        meta: { title: "更换手机" }
      },
      {
        path: "exit",
        name: "exit",
        component: () => import("@/views/account/exit"),
        meta: { title: "退出门店" }
      }
    ]
  }
];
var router = new Router({
  scrollBehavior: () => ({ y: 0 }),
  routes: constantRouterMap
});

//全局前置守卫
router.beforeEach((to, from, next) => {
  if (getId()) {
    if (/login/.test(to.path)) {
      next({ path: "/" });
    } else {
      next();
    }
  } else {
    if (/login/.test(to.path)) {
      next();
    } else {
      next({ path: "/login" });
    }
  }
});

export default router;



