<template>
  <div class="app-wrapper" :class="sidebarOpen ? 'openSidebar' : 'hideSidebar'">

    <div class="head-top clearfix">
      <div class="fl">
        <img class="logo">
        <span class="intro">经纪人平台管理中心</span>
      </div>

      <div class="fr">
        <i class="iconfont icon-msg"></i>
        
        <span class="nav-right"><i class="el-icon-message"></i>前台首页</span>
        <span class="nav-right blue" @click="loginout"><i class="el-icon-message"></i>退出</span>
      </div>
    </div>

    <side-bar class="sidebar-container"/>

    <div class="main-container">
      <!-- <nav-bar/> -->
      <app-main/>
    </div>
  </div>
</template>

<script>
import {loginout} from '@/api/login'
import { sideBar, appMain, navBar } from "./components/index";
import { mapGetters } from "vuex";
export default {
  components: {
    sideBar,
    appMain,
    navBar
  },
  computed: {
    ...mapGetters(["sidebarOpen"])
  },
  methods: {
    loginout () {
      loginout().then(res => {
        if (res.data.code == 0) {
          this.$message.success('退出成功')
          this.$router.push('/login')
        }
      })
    }
  }
};
</script>

<style lang='scss' scoped>
.app-wrapper {
  overflow: auto;
  height: 100%;
}
.head-top {
  position: fixed;
  left: 0;
  right: 0;
  padding: 0 18px;
  height: 60px;
  line-height: 60px;
  background: #2c3e50;
  box-shadow: 0px 4px 6px 0px rgba(201, 211, 223, 1);
  z-index: 2;
  .logo {
    position: relative;
    top: 12px;
    width: 100px;
    height: 40px;
  }
  .intro {
    margin-left: 12px;
    font-size: 18px;
    color: rgba(201, 211, 223, 1);
  }
  i.icon-msg {
    font-size: 20px;
    color: #36A3F7;
    margin-right: 30px;
    cursor: pointer;
  } 
  .nav-right {
    padding: 4px 14px;
    background: rgba(255, 255, 255, 1);
    border-radius: 14px;
    font-size: 14px;
    font-weight: 500;
    color: rgba(0, 160, 107, 1);
    cursor: pointer;
    margin-left: 18px;
    i {
      margin-right: 6px;
    }
    &.blue {
      color: #36A3F7;
    }
  }
}
</style>



